import React from 'react';
import PropTypes from 'prop-types';
import showdown from 'showdown';
import { withStyles } from '../tools/withStyles';
import { Main } from '../components/Main';
import { Secuence } from '../components/Secuence';
import { Text } from '../components/Text';
import anime from 'animejs';

const styles = theme => ({
  root: {},
  blogContainer: {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px'
  },
  postPreview: {
    marginBottom: '30px',
    padding: '20px',
    borderBottom: `1px solid ${theme.color.secondary.main}`
  },
  '@media (max-width: 480px)': {
    blogContainer: {
      padding: 10
    },
    postPreview: {
      padding: 15
    }
  },
  '@media (min-width: 481px) and (max-width: 768px)': {
    blogContainer: {
      padding: 15
    }
  },
  '@media (min-width: 769px) and (max-width: 1024px)': {
    blogContainer: {
      padding: 20
    }
  },
  header: {
    textAlign: 'center',
    marginBottom: '20px'
  },
  title: {
    fontSize: '2.5rem',
    fontWeight: 'bold',
    color: theme.color.primary.main,
    marginBottom: '10px'
  },
  subtitle: {
    fontSize: '1.5rem',
    color: theme.color.secondary.main
  },
  underConstruction: {
    position: 'relative',
    padding: '20px',
    border: `2px dashed ${theme.color.secondary.main}`,
    borderRadius: '5px',
    backgroundColor: theme.color.background.main
  },
  scanline: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: `linear-gradient(to right, transparent 0%, ${theme.color.secondary.main} 50%, transparent 100%)`,
    backgroundSize: '200% 100%',
    animation: 'scanline 1s linear infinite'
  },
  '@keyframes scanline': {
    '0%': {
      backgroundPosition: '0 0'
    },
    '100%': {
      backgroundPosition: '200% 0'
    }
  }
});

class Blog extends React.Component {
  // Initialize showdown converter with options
  converter = new showdown.Converter({
    tables: true,
    simplifiedAutoLink: true,
    strikethrough: true,
    tasklists: true
  });

  static propTypes = {
    classes: PropTypes.object
  };

  componentDidMount() {
    anime({
      targets: '.blog-title',
      opacity: [0, 1],
      translateY: [-50, 0],
      duration: 2000,
      easing: 'easeOutExpo'
    });
  }

  render() {
    const { classes } = this.props;
    
    const underConstructionMessage = `
# JARVIS System Update in Progress...

"In the midst of chaos, there is also opportunity." - Bruce Wayne

Status: Implementing advanced features and security protocols.
ETA: Coming soon...

"Sometimes the truth isn't good enough. Sometimes people deserve more." - Batman
    `;

    // Use showdown to convert markdown to HTML
    const htmlContent = this.converter.makeHtml(underConstructionMessage);

    return (
      <Main className={classes.root}>
        <Secuence stagger>
          <header className={classes.header}>
            <h1 className={`${classes.title} blog-title`}>
              <Text>The Batcave Archives</Text>
            </h1>
            <p className={classes.subtitle}>
              <Text>"I am Iron Man" - Tony Stark</Text>
            </p>
          </header>
          <div className={classes.blogContainer}>
            <div className={classes.underConstruction}>
              <div className={classes.scanline} />
              <div 
                dangerouslySetInnerHTML={{ __html: htmlContent }}
              />
            </div>
          </div>
        </Secuence>
      </Main>
    );
  }
}

export default withStyles(styles)(Blog);
